import produce from "immer"

const base = {
    name: 'base',
    colors: {
        primary: 'hotpink',
        background: '#000000'
    }
}

const light = produce(base, draft => {
    draft.name = "light";
    draft.colors.background = "#F5F6FA";
    draft.colors.text = "#0E0E0E";
    draft.colors.navbar = "#FFFFFF";
});

const dark = produce(base, draft => {
    draft.name = "dark";
    draft.colors.background = "#0E0E0E";
    draft.colors.text = "#F5F6FA";
    draft.colors.navbar = "#000000";
});

export {
    light,
    dark
}