import { useState, useEffect } from 'react';

export function useDarkModeEnable() {
    const [state, stateSet] = useState([false]);

    const handler = () => {
        if (window.matchMedia) {
            if (window.matchMedia('(prefers-color-scheme: dark)').matches) {
                return [true, 'dark'];
            } else {
                return [true, 'light'];
            }
        } else {
            return [false];
        }
    }

    useEffect(() => {
        stateSet(handler());
        window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', () => stateSet(handler()));
    }, []);

    return state;
}